@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.footer {
  background-color: #333;
  color: white;
  padding: 20px 0;
}

.footer p {
  margin: 0;
}

.footer a {
  color: white;
  text-decoration: none;
  margin-right: 10px;
}

.footer i {
  font-size: 24px;
}
