.HistoryView {
	display: flex;
	flex-direction: column;
	overflow: auto;
	height: 100%;
	width: 200px;
	user-select: none;
	box-shadow: -1px 0 5px rgba(0, 0, 0, 0.5);
	background: white;
	color: black;
	transition: background 0.4s ease, color 0.4s ease; 
  }
  
  [data-color-scheme="dark"] .HistoryView {
	background: black;
	color: white;
  }
  
